.HomePage {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  width: 80%;
}

.hero-text {
  color: white;
  text-align: center;
  font-size: 3.6rem;
  span {
    color: #037fff;
  }
}

.h-sub-text {
  text-align: center;
}

.icons {
  display: flex;
  align-items: center;
  justify-content: center;
  padding: 1.5rem 0;

  .icon-holder {
    margin-right: 1rem;

    .icon {
      font-size: 2.8rem;
      color: #a4acc4;
      transition: all .4s ease-in-out;
    }
    .ig:hover {
      color: #405de6;
    }
    .li:hover {
      color: #2867b2;
    }
    .gh:hover {
      color: rgba(158, 63, 158, 0.897);
    }
  }
}
